
































import {Component, Vue} from 'vue-property-decorator';
import {title as t0, novel as n0, comment as c0} from '../novels/nayuta/00_al_01_shitei';
import {title as t1, novel as n1, comment as c1} from '../novels/nayuta/01_or_01_sanserize';
@Component
export default class NayutaPage extends Vue {
    listItem = new Array(
        new Pages(t0, n0, c0, "アルハゼン親子"),
        new Pages(t1, n1, c1, "兄妹"),
    );
    isVertical = false;
}
class Pages{
    title:string ="";
    private novel: string;
    private description:string = "";
    private genru:string = ""
    model:boolean = false;

    constructor(title:string, novel:string, des:string, genru:string){
        this.title = title;
        this.novel = novel;
        this.description = des;
        this.genru = genru;
    }
    getTitle(){
        return this.title;
    }
    getGenru(){
        return this.genru;
    }
    getNovel(){
        return this.novel;
    }
    getComment(){
        return this.description;
    }
    changeModel(){
        this.model = !this.model;
        console.log(this.model)
    }
}
